const sizes = [
  { value: 'xs', label: 'XS', subLabel: 'UK 6' },
  { value: 's', label: 'S', subLabel: 'UK 8' },
  { value: 'm', label: 'M', subLabel: 'UK 10' },
  { value: 'l', label: 'L', subLabel: 'UK 12' }
]

export default class User extends Component {
  state = {
    size: 's',
    occupation: 'Administrator',
    phone: '',
    fName: '',
    lName: '',
  }

  _handleChange = e => this.setState({ [e.target.id]: e.target.value })
  _handleSubmit = e => {
    e.preventDefault()
    const { size, occupation, phone, fName, lName } = this.state
    const { token, onUpdated } = this.props
    const profileData = {
      size,
      occupation,
      phone,
      name: fName + ' ' + lName,
    }
    window.uk.profile.updateProfile(profileData, result => {
      if (result.code) {
        const data = `
          mutation{
            guestMutation{
              profileUpdateOne(record:{
                name: "${profileData.name}"
              }) {
                recordId
                record {
                  email
                  name
                  accountId
                  distinctId
                  profileId
                  phoneNumber
                  customerId
                  subscriptionId
                  current_period_end
                  current_period_start
                  isPaid
                  planId
                  height
                  weight
                  bust
                  waist
                  hip
                  recentOrderId
                  lastVisit
                  updatedAt
                  createdAt
                }
              }
            }
          }`
        axios({
          method: 'post',
          url: window.apiURL,
          data: { query: data },
          headers: {
            authorization: token,
          }
        }).then(() => onUpdated())
      }
    })
  }
  render() {
    const { size } = this.state
    return (
      <div className='user'>
        <h1 className='headerMessage'>Last step! Complete this so we can onboard you.</h1>
        <form onSubmit={this._handleSubmit}>
          <div className='userFields'>
            <label>First name</label>
            <input id='fName' onChange={this._handleChange} placeholder='Input your name' required />
          </div>
          <div className='userFields'>
            <label>Surname</label>
            <input id='lName' onChange={this._handleChange} placeholder='Input your name' required />
          </div>
          <div className='clearfix' />
          <div className='user-phone clearfix userFields'>
            <label>Phone number</label>
            <img src={Flag} alt='' />
            <span>65+</span>
            <input id='phone' className='phone' onChange={this._handleChange} placeholder='Enter your number' className='phone-number' required type='number' />
          </div>
          <label>Occupation</label>
          <div className="select-style">
            <select id='occupation' onChange={this._handleChange} placeholder='ABC'>
              {occupations.map(occupation => <option key={occupation.value} value={occupation.value}>{occupation.label}</option>)}
            </select>
          </div>

          <label style={{ marginTop: 15, marginBottom: 5 }}>Your size</label>
          <div className='sizes'>
            {sizes.map(s => <div
              key={s.value}
              onClick={() => this.setState({ size: s.value })}
              className={`size${size === s.value ? ' active' : ''}`}>
              <div className='size-label'>{`${s.label} - ${s.subLabel}`}</div>
            </div>)}
          </div>
          <div style={{width:'100%'}}>
            <button className='join-now-button'>Join now</button>
          </div>

        </form>
      </div>
    );
  }
}

