import React from 'react';
import Join from "../components/Join";
import Helmet from 'react-helmet'
import join from '../assets/images/join.jpg'
import user from '../assets/images/user.jpg'
import User from "../components/User";
import axios from 'axios'
import Layout from '../components/layout';
import '../assets/css/waitlist.css';
const photo = { join, user }

class JoinWaitlist extends React.Component {
    state = {
        currentPage: 'join',
        error: false,
        profileID: '',
        token: '',
    }
    _checkLogin = (result) => {
    if (result.code === 200) {
        const { data } = result
        const profile = data.profiles[0]
        const { account_email } = profile
        window.uk.profile.updateProfile({ _account_email: account_email })
        if (!profile.occupation) {
        const mtData = `
        mutation{
            guestMutation{
            create(record:{email: "${account_email}"}) {
                recordId
                record {
                    email
                    name
                    accountId
                    distinctId
                    profileId
                    phoneNumber
                    customerId
                    subscriptionId
                    current_period_end
                    current_period_start
                    isPaid
                    planId
                    height
                    weight
                    bust
                    waist
                    hip
                    recentOrderId
                    lastVisit
                    updatedAt
                    createdAt
                }
            }
            }
        }
        `
        axios({
            method: 'post',
            url: window.apiURL,
            data: { query: mtData },
            headers: {
            authorization: data.token,
            }
        }).then(res => {
            this.setState({ profileID: profile._id, token: data.token, currentPage: 'user' })
        })


        } else this.setState({ error: true })
    } else {
        this.setState({ error: true })
    }
    }

    _handleUpdated = () => {
    window.location = 'https://madthread.com/thank-you'
    }
    render() {
        const { currentPage, profileID, token, error } = this.state
        return (
            <Layout>  
                   {error && <div className='error' onClick={() => this.setState({ error: false })}>
                        <div onClick={(e) => e.stopPropagation()}>
                            <h1>Woops.</h1>
                            <div>Email already exists.</div>
                        </div> 
                    </div>}

                    <div className='loading' />

                    <div className='main'>
                    <div className='main-photo' style={{ backgroundImage: `url(${photo[currentPage]})` }} />
                    <div className='main-content-page'>
                        {currentPage === 'join'}
                        <div className='main-content clearfix'>
                        {currentPage === 'join' && <Join onLogin={this._checkLogin} />}
                        {currentPage === 'user' && <User profileID={profileID} token={token} onUpdated={this._handleUpdated} />}
                        </div>
                    </div>
                    </div>
            </Layout>
        );
    }
}

export default JoinWaitlist;