import React, { Component } from 'react'
import Logo from '../assets/images/logo.png'

export default class Join extends Component {
  state = {
    email: '',
    password: '',
    isLoading: false
  }
  
  _handleChange = (e) => this.setState({ [e.target.id]: e.target.value })

  _signUp = (e) => {
    e.preventDefault()
    const { email, password } = this.state
    const { onLogin } = this.props
    this.setState({ isLoading: true })
    console.log(window.uk)
    window.uk.auth.signup({ email, password }, result => {
      this.setState({ isLoading: false })
      onLogin && onLogin(result)
    })
  }

  render() {
    const { email, password } = this.state
    return (
      <div className='join clearfix'>
        <div className='logo clearfix'><img width='218' alt='' src={Logo} /></div>
        <form className='join-form' onSubmit={this._signUp} >
          <label htmlFor='email'>E-mail</label>
          <input required type='email' value={email} onChange={this._handleChange} id='email' placeholder='Enter your email' />
          <label htmlFor='password'>Password</label>
          <input required value={password} onChange={this._handleChange} type='password' id='password' placeholder='Enter your password' />
          <button>Join the Waitlist</button>
        </form>
        <div className="join-tryon-main">
          <span className="join-tryon">Try on styles and order on the spot in our CBD office!</span>
          <br></br>
          <a className="join-tryon-whatsapp" href='https://api.whatsapp.com/send?phone=6581237783' target="_blank" >WhatsApp us</a>
          <span className="join-tryon"> to book an in-person fitting.</span>
        </div>
      </div>

    );
  }
}
